.main {
  display: flex;
  justify-content: center;
}

.catalog {
  display: flex;
  flex-wrap: wrap;
  width: 1024px;
  padding: 50px 40px 0;

  &__product {
    margin-right: 48px;
    margin-bottom: 46px;

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-last-child(-n+4) {
      margin-bottom: 0;
    }
  }
}

.product {
  box-sizing: border-box;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  background-color: $back-color;

  &__photo {
    display: block;
    margin: 0 auto 40px;
    height: 134px;
    width: 160px;
  }

  &__name {
    margin-top: 0;
    margin-bottom: 4px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $main-color;
  }

  &__code {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    margin-bottom: 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__review {
    text-decoration: none;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $main-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-title {
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    color: #616070;
  }

  &__price-value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $main-color;
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 40px;
    border: 1px solid $button-color;
    border-radius: 5px;
    font-size: 14px;
    line-height: 16px;
    color: $back-color;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    background-color: $button-color;
  }

  &__buy-button:hover {
    background-color: $back-color;
    color: $button-color;
  }
}

%active-star {
  background-image: url(./../images/star-active.svg);
}
